import React from "react"
import { Card, CardBody } from "reactstrap"
import { ApiGeneric } from "src/services/apiServices"
class Page extends React.Component {
  componentDidMount() {
    this._requestDashboard()
  }
  _requestDashboard = () => {
    ApiGeneric({
      request: {
        method: "POST",
        path: "Dashboard",
        name: "MyDashboard",
      },
      data: {},
      successCallBack: response => {
        if (response.Data) {
          this.setState({ data: response.Data })
          //Config, Items, Menus, ReportMenus
          /*
            APIUrl 
            Code 
            ItemChildren 
            Name 
            OrderNo 
            RequestData 
            ScreenConfig 
            ScreenGUID 
            ScreenPath 
            Type 
            UIType
          */
        }
      },
      errorCallBack: err => {
        console.log(err)
      },
    })
  }
  render() {
    return <div>Page</div>
  }
}

export default Page
